define([
    "./UtilityCase",
], function (
    UtilityCase,
) {
    /*
    * @description: Objeto que contiene las funciones para la subdivisión de lotes
    */
    const SubDivision = {
        codRequest: null, // @params: Código de la solicitud
        // currentLots: null, // @param: Lotes actuales a modificar
        currentLotsRows: null, // @calculate []: Features de los lotes actuales
        currentPoinLotsRows: null, // @calculate: Features de los puntos de los lotes actuales
        currentLandsRows: null, // @calculate: Features de los predios actuales
        // newPointLots: null, // @param: nuevos puntos lote
        newPointLotsGraphics: null, // @params: nuevos puntos lote como graficos
        newLandsGraphics: null, // @params: nuevos predios como graficos
        attributes: null, // @param: 
        pointLotUrl: null, // @param
        landUrl: null, // @param
        lotUrl: null, // @param
        arancelUrl: null, // @param
        blockUrl: null, // @param
        lotGraphic: null, // @param
        cadastralBlockUrl: null, // @param
        ubigeo: null, // @param
        user: null, // @param
        // geometryLand: null, // @param []: coodernadas x,y de los predios resultantes
        config: null, // @param

        lands: null, // @calculate
        pointLots: [], // @calculate array
        lots: null, // @calculate
        // block: null, // @calculate
        // LandCls: new UtilityCase.Land(),
        // LotCls: new UtilityCase.Lot(),
        // PointLotCls: new UtilityCase.PointLot(),
        caseRequest: null, // @param
        queryBlock: null, // @param

        executeSubdivision() {
            return UtilityCase.getBlockFromLot(this.currentLotsRows[0].geometry, this.blockUrl)
                .then(block => UtilityCase.checkExistLotUrban(this.attributes, block, this.lotUrl))
                .then(block => UtilityCase.translateFieldsBlockToLot(this.lotUrl, block, this.lotGraphic))
                .then(lots => {
                    const tipLot = UtilityCase.calculateTipLot(this.currentLotsRows);
                    return UtilityCase.calculateFieldsOfLot(
                        this.lotUrl,
                        lots,
                        this.ubigeo,
                        this.codRequest,
                        this.user,
                        this.attributes,
                        tipLot
                    )
                })
                .then(lots => {
                    this.lots = lots;
                    return UtilityCase.translateFieldsLotToPointLot(lots, this.pointLotUrl, this.newPointLotsGraphics)
                })
                .then(pointLots => UtilityCase.translateFieldsArancelToPointLot(pointLots, this.arancelUrl))
                .then(pointLots => UtilityCase.calculateFieldsOfPointLot(this.pointLotUrl, this.ubigeo, pointLots))
                .then(pointLots => {
                    this.pointLots = pointLots;
                    return UtilityCase.translateFieldsPointLotToLand(pointLots, this.landUrl, this.newLandsGraphics)
                })
                .then(lands => UtilityCase.calculateIdMznC(lands, this.cadastralBlockUrl, this.ubigeo))
                .then(lands => UtilityCase.calculateIdPred(lands, this.landUrl, this.ubigeo))
                .then(lands => {
                    this.lands = lands;
                    return UtilityCase.getDataOrigin(this.pointLotUrl, this.landUrl, this.currentLotsRows[0])
                })
                .then(results => {
                    this.currentPoinLotsRows = results[0].features;
                    this.currentLandsRows = results[1].features;
                    return UtilityCase.sendDataOriginToHistoric(this.config, this.currentLotsRows, this.currentPoinLotsRows, this.currentLandsRows)
                })
                .then(results => UtilityCase.deleteDataOrigin(this.currentLotsRows, this.currentPoinLotsRows, this.currentLandsRows, this.config))
                .then(results => UtilityCase.addDataNew(this.lots, this.pointLots, this.lands, this.config))
                .then(results => UtilityCase.updateStatusRequests(this.lands, this.codRequests, this.caseRequest, this.ubigeo, this.config))
                .catch(err => { throw err });
        },
    };
    return SubDivision;
});